@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Fonts */
@font-face {
  font-family: 'Dyslexic';
  src: url('/fonts/dyslexic.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Dyslexic';
  src: url('/fonts/dyslexic_bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Dyslexic';
  src: url('/fonts/dyslexic_italic.woff2') format('woff2');
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: 'Dyslexic';
  src: url('/fonts/dyslexic_italic_bold.woff2') format('woff2');
  font-weight: bold;
  font-style: italic;
}
@font-face {
  font-family: 'ModernDense';
  src: url('/fonts/modern_dense.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'ModernDense';
  src: url('/fonts/modern_dense_italic.woff2') format('woff2');
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: 'ModernWide';
  src: url('/fonts/modern_wide.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Serif';
  src: url('/fonts/serif.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Serif';
  src: url('/fonts/serif_italic.woff2') format('woff2');
  font-weight: normal;
  font-style: italic;
}

