body {
  font-family: var(--default-font);
  background: var(--bg-graph);
  color: var(--text-primary);
  overflow: hidden;
  margin: 0;
}

.font-dyslexic { font-family: 'Dyslexic', sans-serif !important; }
.font-modern-dense { font-family: 'ModernDense', sans-serif !important; }
.font-modern-wide { font-family: 'ModernWide', sans-serif !important; }
.font-serif { font-family: 'Serif', serif !important; }

/* Graph SVG Styles */
#graph-svg {
  width: 100%;
  height: 100%;
  cursor: grab;
}

#graph-svg:active {
  cursor: grabbing;
}

.link {
  stroke: var(--link-color);
  stroke-width: 1;
}

.node {
  cursor: pointer;
  transition: stroke-width 3s;
}

.node:hover {
  stroke: #ffffff;
  stroke-width: 1.5;
}

.node.selected {
  stroke: #ffffff;
  stroke-width: 2;
}

.node.dimmed {
  opacity: 0.2;
}

.link.dimmed {
  opacity: 0.1;
}

.label.dimmed {
  opacity: 0.2;
}

.label {
  fill: var(--text-primary);
  font-size: 12px;
  pointer-events: none;
  user-select: none;
}

.sublabel {
  fill: var(--text-secondary);
  font-size: 10px;
  pointer-events: none;
  user-select: none;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #0BA1DD;
}

::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #4a4a4a;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .mobile-hidden {
    display: none;
  }
}

/* Tab Content */
.tab-content {
  max-height: 60vh;
  overflow-y: auto;
}

/* Group Item */
.group-item {
  transition: all 0.2s;
}

.group-item:hover {
  box-shadow:
    0 0.25rem 1.25rem hsl(var(--always-black) / 5%),
    0 0 0 0.5px hsla(var(--border-200) / 0.4);
}

/* Panel Collapse */
.panel-collapsed {
  transform: translateX(-100%);
}

.panel-collapsed #collapse-btn {
  transform: translateX(320px) rotate(180deg);
}

#collapse-btn {
  transition: all 0.3s ease;
}

/* Color Preset */
.color-preset {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  cursor: pointer;
  border: 2px solid transparent;
  transition: all 0.2s;
}

.color-preset:hover {
  transform: scale(1.1);
}

.color-preset.selected {
  border-color: white;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.3);
}

/* Performance optimizations */
.node, .link, .label, .sublabel {
  will-change: auto;
}

.bright-container {
  background-color: transparent;
  box-shadow:
    0 0.25rem 1.25rem hsl(var(--always-black) / 3.5%),
    0 0 0 0.5px hsla(var(--border-300) / 0.15);
  color: var(--text-200-full);
  border-radius: 0.5rem;
}

.bright-container:hover {
  box-shadow:
    0 0.25rem 1.25rem hsl(var(--always-black) / 3.5%),
    0 0 0 0.5px hsla(var(--border-200) / 0.3);
  color: var(--text-000-full);
}

.bright-container:focus-within {
  box-shadow:
    0 0.25rem 1.25rem hsl(var(--always-black) / 7.5%),
    0 0 0 0.5px hsla(var(--border-200) / 0.3);
  color: var(--text-000-full);
}

.bright-container:hover:focus-within {
  box-shadow:
    0 0.25rem 1.25rem hsl(var(--always-black) / 7.5%),
    0 0 0 0.5px hsla(var(--border-200) / 0.3);
  color: var(--text-000-full);
}

.fancy-button {
  background-color: transparent;
  color: var(--text-200-full);
  border-radius: 0.5rem;
}

.fancy-button:hover {
  background-color: var(--bg-300-full);
  color: var(--text-000-full);
}

.fancy-button:focus,
.fancy-button:focus-visible {
  background-color: var(--bg-200-full);
  color: var(--text-200-full);
  outline: none;
}

.fancy-button:hover:focus,
.fancy-button:hover:focus-visible {
  background-color: var(--bg-200-full);
  color: var(--text-200-full);
}
